.card-delete__popup {
    background-color: #FFFFFF;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    width: 430px;
    min-height: 181px;
    margin-left: auto;
    margin-right: auto;
    position: relative;
}

@media screen and (max-width: 589px) {
    .card-delete__popup {
        width: 282px;
        height: 200px;
    }
}