.elements {
    display: grid;
    grid-template-columns: repeat(3, 282px);
    grid-template-rows: repeat(2, 361px);
    grid-row-gap: 20px;
    row-gap: 20px;
    grid-column-gap: 17px;
    -webkit-column-gap: 17px;
            column-gap: 17px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
    width: 100%;
    max-width: 880px;
}

